.loading {
  background-image: url("shared/assets/spinner.svg");
  background-size: 5%;
  background-repeat: no-repeat;
  background-position: center center;
  height: 300px;
}
.header {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 8px;
}
.titles {
  flex-grow: 1;
}
.chartWrapper {
  display: flex;
}
.chartWrapperLegendLeft {
  flex-direction: row-reverse;
}
.chartWrapperLegendRight {
  flex-direction: row;
}
.chartWrapperLegendTop {
  flex-direction: column-reverse;
}
.chartWrapperLegendBottom {
  flex-direction: column;
}
.chart {
  min-width: 350px;
  width: 100%;
  position: relative;
}
.customChartLegendHidden {
  display: none;
}
.chart :global .amcharts-AxisLabel text tspan,
.chart :global .amcharts-Label text tspan {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 12px;
  color: #303030;
  stroke: #303030;
  fill: #303030;
  stroke-opacity: 0;
}
.chart :global .AnnotationContainer .amcharts-Line-group {
  display: none;
}
.chart :global .AnnotationBullet:hover .amcharts-RoundedRectangle-group,
.chart :global .AnnotationContainer:hover .amcharts-Line-group {
  display: inline;
  fill: #de3473;
  stroke: #de3473;
}
.chart :global .AnnotationBullet:hover .amcharts-Label text tspan {
  fill: #ffffff;
}
